      <span class="hljs-tag">&lt;<span class="hljs-name">From-Item</span> <span class="hljs-attr">validate-loading</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"用户名"</span> <span class="hljs-attr">prop</span>=<span class="hljs-string">"uasrName"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model.uasrName"</span> /&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">From-Item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">From-Item</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"密码"</span> <span class="hljs-attr">prop</span>=<span class="hljs-string">"pass"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"password"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model.pass"</span> /&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">From-Item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">From-Item</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"确认密码"</span> <span class="hljs-attr">prop</span>=<span class="hljs-string">"validatePass"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"password"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model.validatePass"</span> /&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">From-Item</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">From</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"bto"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"padding:15px"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">Button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"click"</span>&gt;</span>提 交<span class="hljs-tag">&lt;/<span class="hljs-name">Button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">Button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"resetFields"</span>&gt;</span>重 置<span class="hljs-tag">&lt;/<span class="hljs-name">Button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">const</span> uasrName = <span class="hljs-function">(<span class="hljs-params">rule, value, callback</span>) =&gt;</span> {
      <span class="hljs-keyword">if</span> (!value) {
        <span class="hljs-keyword">return</span> callback(<span class="hljs-keyword">new</span> <span class="hljs-built_in">Error</span>(<span class="hljs-string">"请输入用户名"</span>));
      }
      setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
        callback();
      }, <span class="hljs-number">1000</span>);
    };
    <span class="hljs-keyword">const</span> pass = <span class="hljs-function">(<span class="hljs-params">rule, value, callback</span>) =&gt;</span> {
      <span class="hljs-keyword">if</span> (!value) {
        <span class="hljs-keyword">return</span> callback(<span class="hljs-keyword">new</span> <span class="hljs-built_in">Error</span>(<span class="hljs-string">"请输入密码"</span>));
      }
      callback();
    };
    <span class="hljs-keyword">const</span> validatePass = <span class="hljs-function">(<span class="hljs-params">rule, value, callback</span>) =&gt;</span> {
      <span class="hljs-keyword">if</span> (!<span class="hljs-keyword">this</span>.model.pass) {
        <span class="hljs-keyword">this</span>.$refs.from.validateField(<span class="hljs-string">"pass"</span>, <span class="hljs-string">"blur"</span>);
        <span class="hljs-keyword">return</span>;
      }
      <span class="hljs-keyword">if</span> (!value) {
        <span class="hljs-keyword">return</span> callback(<span class="hljs-keyword">new</span> <span class="hljs-built_in">Error</span>(<span class="hljs-string">"请再次输入密码"</span>));
      }
      <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.model.pass !== value) {
        <span class="hljs-keyword">return</span> callback(<span class="hljs-keyword">new</span> <span class="hljs-built_in">Error</span>(<span class="hljs-string">"两次密码不一致"</span>));
      }
      callback();
    };
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">model</span>: {
        <span class="hljs-attr">uasrName</span>: <span class="hljs-string">""</span>,
        <span class="hljs-attr">pass</span>: <span class="hljs-string">""</span>,
        <span class="hljs-attr">validatePass</span>: <span class="hljs-string">""</span>
      },
      <span class="hljs-attr">rules</span>: {
        <span class="hljs-attr">uasrName</span>: [{ <span class="hljs-attr">validator</span>: uasrName, <span class="hljs-attr">trigger</span>: <span class="hljs-string">"blur"</span> }],
        <span class="hljs-attr">pass</span>: [{ <span class="hljs-attr">validator</span>: pass, <span class="hljs-attr">trigger</span>: <span class="hljs-string">"blur"</span> }],
        <span class="hljs-attr">validatePass</span>: [{ <span class="hljs-attr">validator</span>: validatePass, <span class="hljs-attr">trigger</span>: <span class="hljs-string">"blur"</span> }]
      }
    };
  },
  <span class="hljs-attr">methods</span>: {
    click() {
      <span class="hljs-keyword">this</span>.$refs.from.validate(<span class="hljs-function"><span class="hljs-params">value</span> =&gt;</span> {
        <span class="hljs-keyword">if</span> (value) {
          <span class="hljs-keyword">this</span>.$Toast.success(<span class="hljs-string">"校验通过"</span>);
        }
      });
    },
    resetFields() {
      <span class="hljs-keyword">this</span>.$refs.from.resetFields();
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span> <span class="hljs-attr">scoped</span>&gt;</span><span class="css">
<span class="hljs-selector-tag">h3</span> {
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">0</span>;
  <span class="hljs-attribute">padding</span>: <span class="hljs-number">15px</span> <span class="hljs-number">0</span> <span class="hljs-number">15px</span> <span class="hljs-number">15px</span>;
  <span class="hljs-attribute">color</span>: <span class="hljs-number">#84849a</span>;
  <span class="hljs-attribute">font-weight</span>: normal;
  <span class="hljs-attribute">font-size</span>: <span class="hljs-number">14px</span>;
}
<span class="hljs-selector-class">.bto</span> {
  <span class="hljs-attribute">display</span>: flex;
  <span class="hljs-attribute">justify-content</span>: space-around;
}
