      <span class="hljs-tag">&lt;<span class="hljs-name">From-Item</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"输入框"</span> <span class="hljs-attr">prop</span>=<span class="hljs-string">"uasrName"</span> <span class="hljs-attr">:rule</span>=<span class="hljs-string">"[{required:true,message:'请输入内容'}]"</span>&gt;</span>
