<h3>API</h3>
<h5>From props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>model</td>
<td>表单数据对象</td>
<td>Object</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>rules</td>
<td>表单验证规则</td>
<td>Object</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>status-icon</td>
<td>表单校验时右侧是否开启图标反馈</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>success-icon</td>
<td>status-icon开启时校验成功的图标</td>
<td>String</td>
<td>无</td>
<td>iconuncheck</td>
</tr>
<tr>
<td>error-icon</td>
<td>status-icon开启时校验失败的图标</td>
<td>String</td>
<td>无</td>
<td>iconcuowu</td>
</tr>
<tr>
<td>label-width</td>
<td>表单域标签的宽度</td>
<td>Number</td>
<td>无</td>
<td>70</td>
</tr>
</tbody>
</table>
<h5>Form Methods</h5>
<table>
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>validate</td>
<td>对整个表单进行校验的方法，参数为一个回调函数。该回调函数会在校验结束后被调用，并传入是否校验成功的参数，</td>
<td>Function:(callback: (boolean) =&gt; {}) =&gt; {}</td>
</tr>
<tr>
<td>validateField</td>
<td>对个别表单字段进行校验的方法，返回Promise，检验成功返回success、错误返回error</td>
<td>Function:(prop:string,eventName:string) =&gt; {}</td>
</tr>
<tr>
<td>resetFields</td>
<td>对整个表单进行重置，将所有字段值重置为初始值并移除校验结果</td>
<td>无</td>
</tr>
<tr>
<td>clearValidate</td>
<td>移除所有校验结果</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>FromItem props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>标签文本</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>prop</td>
<td>表单域 model 字段，在使用 validate、resetFields 方法的情况下，该属性是必填的</td>
<td>String</td>
<td>传入 Form 组件的 model 中的字段</td>
<td>无</td>
</tr>
<tr>
<td>rule</td>
<td>表单验证规则</td>
<td>Array</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>cellInline</td>
<td>左右默认行内模式，设置为false时上下分块</td>
<td>Boolean</td>
<td>无</td>
<td>true</td>
</tr>
<tr>
<td>label-width</td>
<td>表单域标签的的宽度</td>
<td>Number</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>validate-loading</td>
<td>校验该项时是否loading</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>FromItem solt</h5>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>Form Item 的内容</td>
</tr>
<tr>
<td>label</td>
<td>标签文本的内容</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>



