</style>
<script>
export default {
  data() {
    return {
      model: {
        uasrName: "",
        age: "",
        file: []
      }
    };
  },
  methods: {
    multipleBeforeUpload(file) {
      return new Promise(() => {
        this.model.file.push(URL.createObjectURL(file));
      });
    }
  }
};
</script>


